.contacts__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  padding: 20px 20px 10px 20px;
}

.contacts__text {
  max-width: 50%;

  &--invite {
    margin-top: 8px;
    font-size: 16px;
    line-height: 20px;
    text-transform: uppercase;
  }

  &--address {
    box-sizing: border-box;
    margin-top: 8px;
    padding-right: 5px;
    font-size: 14px;
    font-family: "Arial", sans-serif;
    line-height: 20px;
  }

  &--city {
    max-width: 100%;
  }
}

.contacts__map {
  width: 100%;
  border: none;
  overflow: hidden;
  min-height: 362px;
  background: $light-gray url("../img/bg-map-mobile@1x.png") no-repeat;
}

@media (min-resolution: $retina) {
  .contacts__map {
    background: $light-gray url("../img/bg-map-mobile@2x.png") no-repeat;
    background-size: cover;
  }
}


.contacts-page {
  background-color: $light-gray;
}

@media (min-width: $tablet-width) {
  .contacts__wrapper {
    @include tablet-mix;
    justify-content: space-between;
    padding: 30px 71px 41px;
  }

  .contacts__map {
    min-height: 396px;
    background: $light-gray url("../img/bg-map-tablet@1x.png") no-repeat;
  }

  @media (min-resolution: $retina) {
    .contacts__map {
      background: $light-gray url("../img/bg-map-tablet@2x.png") no-repeat;
      background-size: cover;
    }
  }

  .contacts__text {
    max-width: calc(50% - 30px);

    &--invite {
      margin-top: 34px;
      margin-left: 0;
      font-size: 20px;
      line-height: 25px;
    }

    &--address {
      margin-top: 33px;
      padding: 0;
      font-size: 16px;
      text-align: end;
    }

    &--city {
      display: block;
      margin-top: 20px;
      max-width: 100%;
    }
  }
}

@media (min-width: $desktop-width) {
  .contacts {
    position: relative;
    padding: 93px 0 105px 0;
  }

  .contacts__wrapper {
    position: relative;
    z-index: 2;
    margin-left: 110px;
    padding: 32px 76px 41px;
    background-color: $base-color;
    max-width: 570px;
  }

  .contacts__text--invite {
    font-size: 20px;
    line-height: 25px;
  }

  .contacts__text--address {
    margin-right: 15px;
    text-align: start;
    font-size: 16px;
    line-height: 20px;
  }

  .contacts__text--city {
    margin-top: 21px;
  }

  .contacts__map {
    position: absolute;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    min-height: 400px;
    background: url("../img/bg-map-desktop@1x.png") no-repeat;
  }

  @media (min-resolution: $retina) {
    .contacts__map {
      background: $light-gray url("../img/bg-map-desktop@2x.png") no-repeat;
      background-size: cover;
    }
  }
}
